  -webkit-transition: 0.5s all;
  transition: 0.5s all;
  justify-content: flex-end; }



/*Всплывающие меню*/
.vert-menu__drop {
  position: absolute;
  width: 100%;
  left: 100%;
  top: -9999em;
  opacity: 0;
  border-left: 1px solid transparent;
  -webkit-transition: 0.5s opacity;
  transition: 0.5s opacity; }


.admin-category-menu .vert-menu__list:hover > .vert-menu__drop {
  opacity: 1;
  top: 0;
  z-index: 1; }


/*Иконки для всплывающих менюшек*/
.vert-menu__list::after {
  content: " ";
  position: absolute;
  border: .6vw solid;
  border-color: transparent transparent transparent #84989a;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  align-self: center; }


.vert-menu__list:hover::after {
  -webkit-transform: scaleX(-1);
  -ms-transform: scaleX(-1);
  transform: scaleX(-1); }



